#app {
  background-color: #20231f;
  font-family: 'Roboto', sans-serif;
  color: white;
  font-size: 1rem;
  line-height: 1.5;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-align: center;
  height: 100%;
  min-height: 100vh;
  width: 100%;
}

.rant-view__title {
  font-size: 2rem;
  font-weight: 700;
  margin: 0;
  padding: 0;
  text-align: center;
  width: 100%;
  background-color: #20231f;
  color: white;
}

#status {
  opacity: 0;
}



/*    List    */

.rant-view__list {
  list-style: none;
  margin: 0 auto;
  padding: 0;
  width: 95%;

}
.rant-view__list-row {
  border: 1px solid #dcdecd;

}
